import Home from '~/containers/Home/container'
// import About from '~/containers/About/container'
export default [
  {
    path: '/',
    exact: true,
    component: Home,
  },
  {
    path: '/:about',
    exact: true,
    component: Home,
  },
  {
    path: '/:about/:conponent',
    exact: true,
    component: Home,
  },
];



